import * as C from '../types';
import { get } from '../api';

export const getDomainList = () => async (dispatch, getState, api) => {
	dispatch({
		type: C.FETCH_DOMAIN_LIST,
	});
	get('/domain/getList')
		.then(({ data }) => {
			dispatch({
				type: C.FINISHED_FETCH_DOMAIN_LIST,
				data,
			});
		})
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_DOMAIN_LIST,
				error,
			});
		});
};

